import { RectRange } from './RectRange';
import { PlainUtils } from '../../../utils/PlainUtils';

/**
 * Merges Class
 */
class Merges {

  /**
   * Merges
   * @param merges
   * @param cols
   * @param rows
   */
  constructor({
    merges = [],
    cols,
    rows,
  }) {
    this.rows = rows;
    this.cols = cols;
    this.data = merges.map(merge => RectRange.valueOf(merge));
    this.index = new Array(rows.len * cols.len);
  }

  /**
   * 
   * @param rectRange
   * @param cb
   */
  getIncludes(rectRange, cb) {
    const { index, data } = this;
    rectRange.each((ri, ci) => {
      const offset = this.getOffset(ri, ci);
      const no = index[offset];
      if (PlainUtils.isNotUnDef(no)) {
        cb(data[no]);
      }
    });
  }

  /**
   * 
   * @param ri
   * @param ci
   * @return {*}
   */
  getOffset(ri, ci) {
    const { cols } = this;
    const { len } = cols;
    return (ri * len) + ci;
  }

  /**
   * 
   * @param ri
   * @param ci
   * @return {null|RectRange}
   */
  getFirstIncludes(ri, ci) {
    const { index, data } = this;
    const offset = this.getOffset(ri, ci);
    const no = index[offset];
    if (PlainUtils.isUnDef(no)) {
      return null;
    }
    const item = data[no];
    if (PlainUtils.isUnDef(item)) {
      return null;
    }
    return item;
  }

  /**
   * 
   * @param rectRange
   * @param checked
   */
  add(rectRange, checked = true) {
    const { index, data } = this;
    // 
    if (checked) {
      this.deleteIntersects(rectRange);
    }
    // 
    const len = data.length;
    data.push(rectRange);
    // 
    rectRange.each((ri, ci) => {
      const offset = this.getOffset(ri, ci);
      index[offset] = len;
    });
  }

  /**
   * 
   * @param rectRange
   */
  delete(rectRange) {
    const { index, data } = this;
    // 
    const { sri, sci } = rectRange;
    const offset = this.getOffset(sri, sci);
    const no = index[offset];
    if (PlainUtils.isUnDef(no)) {
      return;
    }
    data.splice(no, 1);
    // 
    rectRange.each((ri, ci) => {
      const offset = this.getOffset(ri, ci);
      index[offset] = undefined;
    });
    // 
    this.sync(no);
  }

  /**
   * 
   * 
   * @param rectRange
   */
  deleteIntersects(rectRange) {
    this.getIncludes(rectRange, old => this.delete(old));
  }

  /**
   * (,)
   * @param cellRange
   * @return {RectRange}
   */
  union(cellRange) {
    let cr = cellRange;
    const filter = [];
    for (let i = 0; i < this.data.length; i += 1) {
      const item = this.data[i];
      if (filter.find(e => e === item)) {
        continue;
      }
      if (item.intersects(cr)) {
        filter.push(item);
        cr = item.union(cr);
        i = -1;
      }
    }
    return cr;
  }

  /**
   * 
   */
  sync(offset = 0) {
    const { index, data } = this;
    for (let i = offset; i < data.length; i += 1) {
      const rectRange = data[i];
      rectRange.each((ri, ci) => {
        const offset = this.getOffset(ri, ci);
        index[offset] = i;
      });
    }
  }

  /**
   * 
   * @return {RectRange[]}
   */
  getData() {
    return this.data;
  }

  /**
   * 
   * @param data
   */
  setData(data) {
    this.data = data;
  }

}

export {
  Merges,
};
